import React, { Component } from 'react'
import { Link } from 'gatsby'
import { Index } from 'elasticlunr'

import ClasseBadge from '../components/classe-badge'
import MatterBadge from '../components/matter-badge'

import Button from 'react-bootstrap/Button'
import FormControl from 'react-bootstrap/FormControl'
import InputGroup from 'react-bootstrap/InputGroup'
import ListGroup from 'react-bootstrap/ListGroup'
import Nav from 'react-bootstrap/Nav'
import { GoSearch } from 'react-icons/go'

// Search component
export default class Search extends Component {
  constructor(props) {
    super(props)

    this.state = {
      query: ``,
      results: [],
    }
    this.handleClick = this.handleClick.bind(this)
  }

  handleClick() {
    this.setState(() => {
      return {
        query: ``,
        results: [],
      }
    })
  }

  render() {
    return (
      <Nav.Item>
        <InputGroup className="mb-3">
          <FormControl
            placeholder="Rechercher"
            value={this.state.query}
            onChange={this.search}
            aria-label="Barre de recherche"
          />
          <Button variant="secondary" disabled>
            <GoSearch />
          </Button>
        </InputGroup>
        <ListGroup style={{ position: `absolute`, zIndex: `10` }}>
          {this.state.results.map((page) => (
            <ListGroup.Item className="justify-content-between">
              <ClasseBadge classe={page.classe} />
              <MatterBadge node={page} />
              <Link to={`/` + page.path} onClick={this.handleClick}>
                <h4>{page.title}</h4>
              </Link>
              {/* <TagBadges node={page} /> */}
            </ListGroup.Item>
          ))}
        </ListGroup>
      </Nav.Item>
    )
  }

  getOrCreateIndex = () =>
    this.index
      ? this.index
      : // Create an elastic lunr index and hydrate with graphql query results
        Index.load(this.props.data.siteSearchIndex.index)

  search = (evt) => {
    const query = evt.target.value
    this.index = this.getOrCreateIndex()

    this.setState({
      query,
      // Query the index with search string to get an [] of IDs
      results: this.index
        .search(query, {
          fields: {
            title: { boost: 1 },
            tags: { boost: 1 },
            matter: { boost: 2 },
          },
          expand: true, // Accept partial matches,
        })
        // Map over each ID and return the full document
        .map(({ ref }) => this.index.documentStore.getDoc(ref)),
    })
  }
}
