import React from 'react'
import { Helmet } from 'react-helmet'

import Container from 'react-bootstrap/Container'

import { StaticQuery, graphql } from 'gatsby'

import Header from '../components/header'
import Footer from '../components/footer'

import '../scss/layout.scss'

// feuille de style pour l'impression
// TODO chargement conditionnel à la reveal ?print-pdf dans l'adresse
import '../assets/paper.css'

export default ({ children, location }) => (
  <StaticQuery
    query={graphql`
      query SearchIndexQuery {
        siteSearchIndex {
          index
        }
        site {
          siteMetadata {
            siteUrl
          }
        }
      }
    `}
    render={(data) => (
      <React.Fragment>
        <Helmet defaultTitle="lyceum.fr">
          <html lang="fr" />
          <meta name="og:type" content="website" />
          <meta name="og:site_name" content="lyceum" />
          <meta
            property="og:description"
            content="Un site open-source pour les lycéens."
          />
          <meta
            property="og:url"
            content={`${data.site.siteMetadata.siteUrl}${location.pathname}`}
          />
          <meta name="robots" content="index, follow" />
          <link
            rel="canonical"
            href={`${data.site.siteMetadata.siteUrl}${location.pathname}`}
          />
        </Helmet>
        <div class="back-to-present">
          Vous êtes sur une version archivée de lyceum.fr de l'année 2021/2022.&nbsp;
          <a href="https://lyceum.fr">Revenir au présent.</a>
        </div>
        <Header pathname={location.pathname} data={data} />
        <Container>{children}</Container>
        <Footer />
      </React.Fragment>
    )}
  />
)
